import React from "react";
import { Navbar, Form, Button, Nav, Container } from "react-bootstrap";
import obj from "../name";
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// const isLogin = 0;
const Header = () => {
  return (
    <>
      <Navbar
        className="p-2"
        bg="dark"
        expand="lg"
        variant="dark"
        collapseOnSelect
      >
        <Container>
          <Navbar.Brand href="#">{obj.name}</Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: "100px" }}
              navbarScroll
            >
              <Nav.Link href="#action1">Home</Nav.Link>
              <Nav.Link href="#action2">Link</Nav.Link>
            </Nav>
            <Nav className="ml-auto">
              <Nav.Link href="3Cart" className="me-3">
                {/* <FontAwesomeIcon icon="fa-solid fa-cart-shopping" /> */}
                Cart &nbsp;<i className="fa-solid fa-cart-shopping"></i>
              </Nav.Link>
            </Nav>
            
            <Form className="d-flex">
              <Button variant="outline-danger m-2">Sign Up</Button>
              <Button variant="outline-success m-2">Login</Button>
              <Button variant="m-2"><i className="fa-solid fa-user"></i></Button>
            </Form>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  );
};

export default Header;
